import BaseApiService from "./BaseApiService";
const DEFAULT_PATH = `/custom/api/user/profile`;

class ProfileService extends BaseApiService {
  findDelegateByName = (search: any) => {
    const url = `${DEFAULT_PATH}/manage/filter/assignee?loanUid=${search.loanId}&stageName=${search.stageName}&name=${search.name}`;
    return this.makeGetRequestWithAuth(url);
  };

  findAssignee = (start = 0, limit = 10, search: string) => {
    const url = `${DEFAULT_PATH}/search?start=${start}&limit=${limit}${search}`;
    return this.makeGetRequestWithAuth(url);
  };
}

export default new ProfileService();
